* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow: hidden;
  background: black;
}

.birthday-area {
  opacity: 0.3;
  position: absolute;
  top: 0;
  left: 0;
}

.birthday-image {
  opacity: 0.8;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 608px;
  height: 608px;
  filter: drop-shadow(5px 5px 70px green);
}